import React, { useState, useEffect } from 'react';
import { useCart } from './CartContext';
import { useNavigate } from 'react-router-dom';
import { useUser } from './UserContext';
import AddressPopup from './AddressPopup';
import './Order.css';

const API_BASE_URL = 'https://connnet4you-server.onrender.com';

const Order = () => {
  const { cart, cartLoaded, addToCart } = useCart();
  const items = Object.values(cart);
  const navigate = useNavigate();
  const { user } = useUser();

  const [showAddressPopup, setShowAddressPopup] = useState(false);
  const [addresses, setAddresses] = useState([]);
  const [address, setAddress] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState('');
  const [tempAddress, setTempAddress] = useState({
    name: '',
    street: '',
    city: '',
    zip: '',
    phone: '',
  });
  const [selectedItem, setSelectedItem] = useState(null);

  const total = items.reduce((sum, item) => sum + Number(item.price) * item.quantity, 0);

  useEffect(() => {
    const fetchAddresses = async () => {
      if (!user?.id) return;
      try {
        const token = localStorage.getItem('authToken');
        const res = await fetch(`${API_BASE_URL}/api/address`, {
          headers: { Authorization: `Bearer ${token}` }
        });
        if (!res.ok) throw new Error('Failed to fetch addresses');
        const data = await res.json();
        setAddresses(data);
        if (data.length > 0) setAddress(data[0]);
        else setAddress(null);
      } catch (error) {
        console.error('Error fetching addresses:', error);
        setAddresses([]);
        setAddress(null);
      }
    };

    fetchAddresses();
  }, [user]);

  const handleAddressSubmit = async () => {
    try {
      const token = localStorage.getItem('authToken');
      if (!user?.id) {
        alert('User ID missing. Please log in again.');
        return;
      }

      const addressPayload = { ...tempAddress };
      if (tempAddress.id) addressPayload.id = tempAddress.id;

      const response = await fetch(`${API_BASE_URL}/api/address`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(addressPayload),
      });

      if (!response.ok) throw new Error('Failed to save address');
      const savedAddress = await response.json();

      if (tempAddress.id) {
        setAddresses(prev => prev.map(addr => (addr.id === savedAddress.id ? savedAddress : addr)));
      } else {
        setAddresses(prev => [...prev, savedAddress]);
      }

      setAddress(savedAddress);
      setShowAddressPopup(false);
      setTempAddress({ name: '', street: '', city: '', zip: '', phone: '' });
    } catch (error) {
      console.error('Error saving address:', error);
      alert('There was an error saving your address. Please try again.');
    }
  };

  const handleOrder = async () => {
    if (!paymentMethod) return alert('Please select a payment method');
    if (!address) {
      alert('Please select or enter your address.');
      setShowAddressPopup(true);
      return;
    }

    const token = localStorage.getItem('authToken');
    if (!token) {
      alert('You must be logged in to place an order.');
      navigate('/');
      return;
    }

    const orderData = {
      items,
      total,
      address,
      paymentMethod,
      orderDate: new Date().toISOString(),
    };

    try {
      const response = await fetch(`${API_BASE_URL}/api/orders`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(orderData),
      });

      if (!response.ok) throw new Error('Failed to place order');
      const result = await response.json();

      const fullOrder = { ...orderData, orderId: result.orderId };
      localStorage.setItem('orderSummary', JSON.stringify(fullOrder));

      // Use user.shop_slug for route param, fallback to 'demo'
      const shopSlug = user?.shop_slug || 'demo';

      if (paymentMethod === 'cod') {
        navigate(`/shop/${shopSlug}/order-summary`);
      } else {
        navigate(`/shop/${shopSlug}/payment`, { state: { order: fullOrder } });
      }
    } catch (error) {
      console.error('Order placement failed:', error);
      alert('Failed to place order. Please try again.');
    }
  };

  const handleQtyChange = (item, delta) => {
    const newQty = Math.max(0, item.quantity + delta);
    const diff = newQty - item.quantity;
    if (diff !== 0) addToCart(item, diff);
  };

  if (!cartLoaded) return <div className="order-page"><h2>Loading cart...</h2></div>;
  if (items.length === 0) return <div className="order-page"><h2>No items in cart to order.</h2></div>;

  return (
    <div className="order-page">
      <h1> Your Cart</h1>
      <div className="order-list">
        {items.map(item => (
          <div key={item.id} className="order-row">
            <img
              src={process.env.PUBLIC_URL + item.image}
              alt={item.name}
              className="order-img"
              onClick={() => setSelectedItem(item)}
            />
            <div className="order-details">
              <h3>{item.name}</h3>
              <div className="qty-controls">
                <button onClick={() => handleQtyChange(item, -1)} disabled={item.quantity <= 1}></button>
                <span>{item.quantity}</span>
                <button onClick={() => handleQtyChange(item, 1)}>+</button>
              </div>
            </div>
            <div className="order-price">{(item.price * item.quantity).toFixed(2)}</div>
          </div>
        ))}
      </div>

      <div className="order-total">
        <h2>Total: {total.toFixed(2)}</h2>
      </div>

      {addresses.length > 0 ? (
        <div className="address-list">
          <h3>Select Delivery Address</h3>
          {addresses.map(addr => (
            <div key={addr.id} className="address-item-wrapper">
              <label className="address-item">
                <input
                  type="radio"
                  name="selectedAddress"
                  value={addr.id}
                  checked={address?.id === addr.id}
                  onChange={() => setAddress(addr)}
                />
                <div>
                  {addr.name}, {addr.street}, {addr.city} - {addr.zip}
                  <br />
                  Phone: {addr.phone}
                </div>
              </label>
              <button
                className="edit-address-btn"
                onClick={() => {
                  setTempAddress(addr);
                  setShowAddressPopup(true);
                }}
              >
                Edit
              </button>
            </div>
          ))}
          <button
            className="add-new-address-btn"
            onClick={() => {
              setTempAddress({ name: '', street: '', city: '', zip: '', phone: '' });
              setShowAddressPopup(true);
            }}
          >
            Add New Address
          </button>
        </div>
      ) : (
        <button
          onClick={() => {
            setTempAddress({ name: '', street: '', city: '', zip: '', phone: '' });
            setShowAddressPopup(true);
          }}
        >
          Enter Address
        </button>
      )}

      <div className="payment-options">
        <label>
          <input
            type="radio"
            value="cod"
            checked={paymentMethod === 'cod'}
            onChange={() => setPaymentMethod('cod')}
          />
          Cash on Delivery
        </label>
        <label>
          <input
            type="radio"
            value="online"
            checked={paymentMethod === 'online'}
            onChange={() => setPaymentMethod('online')}
          />
          Pay Online
        </label>
      </div>

      <button onClick={handleOrder}>Place Order</button>

      {showAddressPopup && (
        <AddressPopup
          tempAddress={tempAddress}
          setTempAddress={setTempAddress}
          onClose={() => setShowAddressPopup(false)}
          onSubmit={handleAddressSubmit}
        />
      )}

      {selectedItem && (
        <div className="address-popup-overlay" onClick={() => setSelectedItem(null)}>
          <div className="address-popup" onClick={e => e.stopPropagation()}>
            <button className="close-btn" onClick={() => setSelectedItem(null)}>&times;</button>
            <h2>{selectedItem.name}</h2>
            <img src={process.env.PUBLIC_URL + selectedItem.image} alt={selectedItem.name} />
            <p>{selectedItem.description || 'No description available.'}</p>
            <p><strong>Price:</strong> {Number(selectedItem.price).toFixed(2)}</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default Order;
